export default {
	data() {
		return {
			// 第三方登录参数
			face: '',
			nickname: '',
			openIdOrUid: ''
		}
	},
	methods: {







































































		// 表单提交事件
		formSubmit(e) {
			// console.log(e)
			const username = e.detail.value.username
			const password = e.detail.value.password

			// 发起注册/登录请求
			uni.request({
				url: `${this.$api}/user/registOrLogin?qq=122212489`,
				data: {
					"username": username,
					"password": password
				},
				method: "POST",
				success: (res) => {
					if (res.data.status == 200) {
						const userInfo = res.data.data
						// console.log(userInfo)

						// 保存用户信息在本地仓储
						uni.setStorageSync("userToken", userInfo)

						// 跳转到taber页面个人中心
						uni.switchTab({
							url: '../me/me'
						})
					} else if (res.data.status == 500) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							image: "../../static/icos/error.png"
						})
					}
				}
			})
		},

		// 实现在微信小程序端的微信登录
		wxLogin(e) {
			console.log(e.detail.userInfo)
			// 通过微信开发能力 获得微信用户的基本信息
			const userInfo = e.detail.userInfo
			uni.setStorageSync("userToken", userInfo)
			// 提示用户登录成功
			uni.showToast({
				title: "登录成功",
				mask: false,
				duration: 1500
			})
			// 跳转到taber页面个人中心
			uni.switchTab({
				url: '../me/me'
			})


			// 实现微信登录
			// uni.login({
